import React, {useEffect, useRef, useState} from 'react'

export const HorizontalScroll = ({children, className}) => {
    const wrapper = useRef(null);
    const [isBottom, setIsBottom] = useState(false)

    const handleIn = (e) => {
        console.log('in')
        console.log(e.target);
    }

    const handleScroll = (e) => {
        const window = e.currentTarget;
        // const windowBottomPosition = Math.round(wrapper.current.scrollY + wrapper.current.innerHeight)
        // console.log(windowBottomPosition)

        if(wrapper.current.getBoundingClientRect().top <= 0) {
           setIsBottom(true);
           window.scrollTo(0, Number(wrapper.getBoundingClientRect().bottom));
            console.log()
            console.log( wrapper.current.getBoundingClientRect().left)
        }
    }

    const setHandler = () => {
        console.log('isBottom');
    }

    useEffect(() => {
        isBottom && setHandler();
    }, [isBottom])

    useEffect(() => {
        window.addEventListener('scroll', handleScroll)

        return () => {
            window.removeEventListener('scroll', handleScroll)
        }
    }, [])

    return (
        <div ref={wrapper} onScroll={handleScroll} className={`horizontal-scroll ${className}`}>
            {children}
        </div>
    )
}